import { Component } from '@angular/core';
import { ViewController, IonicPage, NavParams } from 'ionic-angular';
import { CoreCourseHelperProvider } from '@core/course/providers/helper';
import { CoreSitesProvider } from '@providers/sites';
/**
 * Component that displays some help regarding the CoreLoginSitePage.
 */
@IonicPage({ segment: 'addon-landing-popup' })
@Component({
    selector: 'addon-landing-popup',
    templateUrl: 'addon-landing-popup.html',
})
export class AddonLandingPopupPage {
    
    protected data: any;
    constructor(params: NavParams, private viewCtrl: ViewController,
        private courseHelper: CoreCourseHelperProvider, private sitesProvider: CoreSitesProvider) { 
        this.data = params.get('data');
    }

    /**
     * Close help modal.
     */
    closeHelp(): void {
        this.viewCtrl.dismiss();
    }

    openActivity(moduleid?: number, courseid?: number, modname?: string) {
        this.courseHelper.navigateToModule(moduleid, this.sitesProvider.getCurrentSiteId(), courseid, undefined, modname);
        this.closeHelp();
    }
}



